  font: 700 24px/1.2 "BrandonGrotesque", sans-serif;
  color: #22232d;
  text-transform: uppercase;
}

.teamSlider__slideHeading i {
  margin: 0 13px 0 0;
  font-size: 32px;
  color: rgba(34, 35, 45, 0.3);
}

.teamSlider__text {
  max-width: 500px;
  margin: 24px 0;
}

.teamSlider__link {
  font: 700 16px/1.6 "BrandonGrotesque", sans-serif;
  color: #1c70e8;
}

.teamDescrSlider__controls {
  margin: 0 0 20px;
  display: flex;
  justify-content: flex-start;
}

@media (min-width: 960px) {
  .teamDescrSlider__controls {
    display: none;
  }
}

.teamDescrSlider__btn {
  width: 16px;
  height: 24px;
  border: none;
  position: relative;
  background-color: transparent;
  transition: .3s;
}

.teamDescrSlider__btn:hover, .teamDescrSlider__btn:active {
  opacity: .3;
}

.teamDescrSlider__btn::before {
  content: "";
  display: block;
  width: 2px;
  height: 70%;
  position: absolute;
  top: -2px;
  left: 50%;
  transform: rotate(-45deg);
  background-color: #22232d;
}

.teamDescrSlider__btn::after {
  content: "";
  display: block;
  width: 2px;
  height: 70%;
  position: absolute;
  top: 37%;
  left: 50%;
  transform: rotate(45deg);
  background-color: #22232d;
}

.teamDescrSlider__btn:not(:last-child) {
  margin-right: 30px;
}

.teamDescrSlider__btn-prev {
  transform: rotate(180deg);
}

.teamDescrSlider__btn.slick-disabled {
  opacity: .15;
  cursor: default;
}
